<template>
    <div class="row">
        <div :class="[medicationRequests.entry != null && medicationRequests.entry.length > 0 ? 'col-md-8' : '']">
            <div class="card" ref="document" id="section-to-print">
                <div class="card-header">
                    <h2>{{ dischargeData.title }}</h2>
                    <p>{{ dischargeData.date }}</p>
                    <div class="d-flex justify-content-between excludeFromPrint">
                        <button v-if="dischargeData.id != null && patientData.id != null && $AuthorizedRole([2])" class="btn btn-primary" @click="openNewTabCreatePrescription">
                            <vue-feather class="align-middle icon-margin" type="file-plus"></vue-feather>
                            <span class="align-middle">Rezept erstellen</span>
                        </button>
                        <button class="btn btn-outline-primary print-hide" onclick="window.print()">
                            <vue-feather class="align-middle icon-margin" type="printer"></vue-feather>
                            <span class="align-middle">Ausdrucken</span>
                        </button>
                    </div>
                </div>
                <ul class="list-group list-group-flush">
                    <li class="list-group-item">
                        <div v-if="patientData.id != null && patientData.name[0] != null">
                            <h5>
                                {{ patientData.name[0].given + " " + patientData.name[0].family }}
                            </h5>
                            <div class="form-group row">
                                <div class="col-md-3"><b>Geburtstag</b></div>
                                <div class="col-md-9">{{ patientData.birthDate }}</div>
                            </div>
                            <div class="form-group row">
                                <div class="col-md-3"><b>Geschlecht</b></div>
                                <div class="col-md-9">{{ patientData.gender }}</div>
                            </div>
                            <div class="form-group row">
                                <div class="col-md-3"><b>Tel. Nr.</b></div>
                                <div class="col-md-9">{{ patientData.telecom[0].value }}</div>
                            </div>
                            <div class="form-group row">
                                <div class="col-md-3"><b>Adresse</b></div>
                                <div class="col-md-9">
                                    {{ patientData.address[0].line[0] + ", " + patientData.address[0].city }}
                                </div>
                            </div>
                        </div>
                    </li>
                    <template v-for="section in dischargeData.section" :key="section">
                        <div class="preventBreak">
                            <li v-if="section.text.div != ''" class="list-group-item preventBreak">
                                <h5>{{ section.title }}</h5>
                                <div>{{ section.text.div }}</div>
                            </li>
                        </div>
                    </template>
                </ul>
            </div>
        </div>
        <div v-if="medicationRequests.entry != null && medicationRequests.entry.length > 0" class="col-md-4">
            <div class="card">
                <div class="card-header">
                    <h5>Verschriebene Medikamente</h5>
                </div>
                <ul class="list-group list-group-flush">
                    <li v-for="item in medicationRequests.entry" :key="item" class="list-group-item hoverable"
                        @click="openViewPrescription(item.resource.id)">
                        <div><b>{{ item.resource.medicationCodeableConcept.coding[0].display }}</b></div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
export default ({
    name: "Care Plans",
    data() {
        return {
            dischargeUrl: '/Composition/' + this.$route.params.dischargeId,
            patientUrl: '/Patient/' + this.$route.params.patientId,
            medicationRequestUrl: '/MedicationRequest?subject=' + this.$route.params.patientId,
            dischargeData: {},
            patientData: {},
            medicationRequests: [] // prescriptions created from this discharge summary
        }
    },
    mounted() {
        console.log(this.$apiUrl + this.dischargeUrl)
        console.log(this.$apiUrl + this.patientUrl)
        console.log(this.$apiUrl + this.medicationRequestUrl)
        fetch(this.$apiUrl + this.dischargeUrl, {
            credentials: 'include',
        })
            .then(response => response.json())
            .then(data => this.dischargeData = data)
            .then(console.log(this.dischargeData))

        fetch(this.$apiUrl + this.patientUrl, {
            credentials: 'include',
        })
            .then((response) => response.json())
            .then((data) => (this.patientData = data))
            .then(console.log(JSON.stringify(this.patientData)))

        fetch(this.$apiUrl + this.medicationRequestUrl, {
            credentials: 'include',
        })
            .then((response) => response.json())
            .then((data) => this.medicationRequests.entry = data.entry.filter(x => x.resource.supportingInformation != null && x.resource.supportingInformation[0].id == this.$route.params.dischargeId))
            .then(console.log(this.medicationRequests))
    },
    methods: {
        openNewTabCreatePrescription() {
            var routeData = this.$router.resolve({ name: 'CreatePrescriptionFromDischarge', params: { id: this.patientData.id, dischargeId: this.dischargeData.id } });
            window.open(routeData.href, '_blank');
        },
        openViewPrescription(medicationRequestId) {
            if (medicationRequestId != null && this.patientData.id != null) {
                console.log("test");
                this.$router.push({ name: 'ViewPrescription', params: { patientId: this.patientData.id, prescriptionId: medicationRequestId } });
            }
        },
    }
})
</script>

<style scoped>
.card {
    text-align: left;
    margin: 2em 1em;
}

.card-header {
    padding: 3em;
}

li {
    padding: 2em;
}

button>a,
button>a:hover,
button>a:focus,
button>a:active {
    color: inherit;
    text-decoration: none;
}

    .icon-margin {
        margin-right: 0.3rem;
    }

.hoverable:hover {
    cursor: pointer;
    background-color: #e3f2fd;
}
</style>

<style>
@media print {
    body * {
        visibility: hidden;
    }

    #section-to-print,
    #section-to-print * {
        visibility: visible;
    }

    #section-to-print {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        margin: 0;
    }

    .card-header {
        padding-top: 1.5em !important;
        padding-bottom: 1.5em !important;

    }

    .li {
        padding-top: 1.5em !important;
        padding-bottom: 1.5em !important;
    }

    .excludeFromPrint {
        display: none !important;
    }
}
</style>